import { useParams } from 'react-router-dom';
import axios from 'axios';
import { useEffect, useState } from 'react';
import Table from 'react-bootstrap/Table';
import Button from 'react-bootstrap/Button';
import { useNavigate } from 'react-router-dom';
import ButtonGroup from 'react-bootstrap/ButtonGroup';

export default function BoardInfo() {
  const navigater = useNavigate();
  let { board_id } = useParams();
  let [board, setBoard] = useState({});
  function boardInfo() {
    axios.get(`http://localhost:80/board/${board_id}`).then((result) => {
      setBoard({ ...result.data[0] });
    });
  }
  let hendlerDelete = () => {
    let real = window.confirm(' ?');
    if (real) {
      axios.delete(`http://localhost:80/board/${board_id}`).then((result) => {
        navigater('/Board/list');
      });
    }
  };

  useEffect(() => {
    boardInfo();
  }, [board_id]);

  return (
    <Table striped>
      <thead>
        <tr>
          <th>id</th>
          <th>title</th>
          <th>content</th>
          <th>wdt</th>
          <th>writer</th>
        </tr>
      </thead>
      <tbody>
        <tr>
          <td>{board.board_id}</td>
          <td>{board.title}</td>
          <td>{board.content}</td>
          <td>{board.wdt}</td>
          <td>{board.writer}</td>
        </tr>
      </tbody>
      <ButtonGroup aria-label="Basic example">
        <Button
          onClick={() => {
            navigater(`/Board/update/${board_id}`);
          }}
        >
          
        </Button>{' '}
        <Button onClick={hendlerDelete} variant="warning">
          
        </Button>
      </ButtonGroup>
    </Table>
  );
}
